<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <span>能源类型</span>
          <el-select v-model="energyVal" placeholder="">
            <el-option
              v-for="item in energyOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div class="tabAll">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <div class="tabAll1">
        <div>
          <div
            v-for="(item, index) in tab"
            :key="index"
            :class="{ isTabActive: tabIndex == index }"
            @click="changeIndex(index)"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <div style="position: relative; height: 300px">
        <div class="tabel">
          <el-table :data="tableData" height="300">
            <template v-for="(item, index) in tableHeader">
              <el-table-column
                :label="item.name"
                :width="item.with"
                :key="index"
              >
                <template slot-scope="scope">
                  <!-- <el-button
                    type="text"
                    size="small"
                    @click="handleClick(scope.row)"
                    >修改</el-button
                  > -->
                  {{getRowData(scope.row,item.key,item.name)}}
                </template>
              </el-table-column>
            </template>
          </el-table>
        </div>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <e-Chart :arry="echartData"></e-Chart>
      </div>
    </div>
  </div>
</template>
<script>
import eChart from "./components/balabceEchart.vue";
import { places, energyBalance } from "@/network/energyTrendAnalysis";
export default {
  components: {
    eChart,
  },
  data() {
    return {
      timeTab: ["日", "月", "年"],
      tTIndex: 0,
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      energyOption: [
        "标煤",
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      energyVal: "标煤",
      date1: "",
      date2: "",
      tableData: [],
      areaId: "",
      tab: ["园区能耗", "企业能耗", "设备能耗"],
      tabIndex: 0,
      tableHeader: [],
      echartData: {},
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    getRowData(row,key,name){
      let val = row[key]
      if(name == '标煤' || name == '电'){
        val = this.toFixedTwo(val)
      }
      return val
    },
    nodeClick(item) {
      if (item.isClick) {
        this.areaId = item.id;
        this.energyBalanceFun();
      }
    },
    placesFun() {
      places()
        .then((res) => {
          let flag = false;
          res.content.forEach((item) => {
            let idFirst = item.id.substring(0, 1);
            if (idFirst == "A" || idFirst == "C") {
              item["isClick"] = true;
              if (!flag) {
                this.areaId = item.id;
                flag = true;
              }
            } else {
              item["isClick"] = false;
            }
          });
          this.data = this.$arrToTree(res.content);
          this.energyBalanceFun();
        })
        .catch((err) => {});
    },
    energyBalanceFun() {
      energyBalance(
        this.timeTab[this.tTIndex],
        this.energyVal,
        this.date1,
        this.date2,
        this.areaId
      )
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.balanceContent = content;
            this.searchData();
          }
        })
        .catch((err) => {});
    },
    changeTimeTab(index) {
      this.tTIndex = index;
      this.energyBalanceFun();
    },
    changeIndex(index) {
      this.tabIndex = index;
      this.searchData();
    },
    /**
     * 根据标签获取显示不同的数据
     * **/
    searchData() {
      if (this.tabIndex == 0) {
        this.tableData = this.balanceContent.area;
        this.tableHeader = [
          {
            name: "园区编号",
            key: "AreaId1",
            with: "150",
          },
          {
            name: "园区名称",
            key: "AreaName1",
            with: "150",
          },
          {
            name: "日期",
            key: "DateStr1",
            with: "150",
          },
          {
            name: "标煤",
            key: "TCEQty1",
            with: "",
          },
          {
            name: "电",
            key: "ElecQty1",
            with: "",
          },
          {
            name: "水",
            key: "WaterQty1",
            with: "",
          },
          {
            name: "蒸汽",
            key: "SteamQty1",
            with: "",
          },
          {
            name: "天然气",
            key: "GasQty1",
            with: "",
          },
          {
            name: "氮气",
            key: "N21",
            with: "",
          },
          {
            name: "压缩空气",
            key: "CompressedAirQty1",
            with: "",
          },
        ];
        this.echartData = {
          data_1: this.balanceContent.areazhe.data1_1,
          data_2: this.balanceContent.areazhe.data1_2,
          data_3: this.balanceContent.areazhe.data1_3,
          data_4: this.balanceContent.areazhe.data1_4,
        };
      } else if (this.tabIndex == 1) {
        this.tableData = this.balanceContent.company;
        this.tableHeader = [
          {
            name: "企业编号",
            key: "CompanyId2",
            with: "150",
          },
          {
            name: "企业名称",
            key: "CompanyName2",
            with: "150",
          },
          {
            name: "日期",
            key: "DateStr2",
            with: "150",
          },
          {
            name: "标煤",
            key: "TCEQty2",
            with: "",
          },
          {
            name: "电",
            key: "ElecQty2",
            with: "",
          },
          {
            name: "水",
            key: "WaterQty2",
            with: "",
          },
          {
            name: "蒸汽",
            key: "SteamQty2",
            with: "",
          },
          {
            name: "天然气",
            key: "GasQty2",
            with: "",
          },
          {
            name: "氮气",
            key: "N22",
            with: "",
          },
          {
            name: "压缩空气",
            key: "CompressedAirQty2",
            with: "",
          },
        ];
        this.echartData = {
          data_1: this.balanceContent.companyzhe.data2_1,
          data_2: this.balanceContent.companyzhe.data2_2,
          data_3: this.balanceContent.companyzhe.data2_3,
          data_4: this.balanceContent.companyzhe.data2_4,
        };
      } else {
        this.tableData = this.balanceContent.eqm;
        this.tableHeader = [
          {
            name: "所属企业编号",
            key: "CompanyId3",
            with: "150",
          },
          {
            name: "所属企业名称",
            key: "CompanyName3",
            with: "150",
          },
          {
            name: "设备编号",
            key: "EnergyEqmId3",
            with: "150",
          },
          {
            name: "设备名称",
            key: "EnergyEqmName3",
            with: "150",
          },
          {
            name: "日期",
            key: "DateStr3",
            with: "150",
          },
          {
            name: "标煤",
            key: "TCEQty3",
            with: "",
          },
          {
            name: "电",
            key: "ElecQty3",
            with: "",
          },
          {
            name: "电(尖)",
            key: "ElecJQty3",
            with: "",
          },
          {
            name: "电(峰)",
            key: "ElecFQty3",
            with: "",
          },
          {
            name: "电(平)",
            key: "ElecPQty3",
            with: "",
          },
          {
            name: "电(谷)",
            key: "ElecGQty3",
            with: "",
          },
          {
            name: "水",
            key: "WaterQty3",
            with: "",
          },
          {
            name: "蒸汽",
            key: "SteamQty3",
            with: "",
          },
          {
            name: "天然气",
            key: "GasQty3",
            with: "",
          },
          {
            name: "氮气",
            key: "N23",
            with: "",
          },
          {
            name: "压缩空气",
            key: "CompressedAirQty3",
            with: "",
          },
        ];
        this.echartData = {
          data_1: this.balanceContent.eqmzhe.data3_1,
          data_2: this.balanceContent.eqmzhe.data3_2,
          data_3: this.balanceContent.eqmzhe.data3_3,
          data_4: this.balanceContent.eqmzhe.data3_4,
        };
      }
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = val;
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
}
.right {
  flex-grow: 1;
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
  overflow: hidden;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  width: 100%;
  margin: 0rem 0 0.2rem 0;
  position: absolute;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.02rem;
    .normalBk();
  }
}
.isActive {
  background: #0056dc !important;
}

.tabAll1 {
  display: flex;
  justify-content: space-between;
  padding: 0 0.41rem 0 0;
  padding-top: 0.12rem;
  align-items: flex-end;
  // border-bottom: 1px solid rgba(34, 175, 237, 0.5);
  > div {
    display: flex;
    > div {
      cursor: pointer;
    }
  }
  > div:first-child > div {
    font-size: 0.18rem;
    padding: 0 0.32rem;
    height: 0.4rem;
    border-radius: 0.04rem 0.04rem 0 0;
    margin: 0 0.03rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.isTabActive {
  background: #0056dc;
  font-size: 0.18rem;
  letter-spacing: 0.01rem;
}
</style>