<template>
  <div class="Pechart">
    <div class="echart" ref="echart"></div>
  </div>
</template>
<script>
export default {
  props: {
    arry: {
      type: Object,
      default: () => [],
    },
  },
  data() {
    return {};
  },
  watch: {
    arry(val) {
      this.setEchart(val);
    },
  },
  mounted() {},
  methods: {
    setEchart(arry) {
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          top: "2%",
          textStyle: {
            color: "#fff",
          },
          type: "scroll",
          orient: "horizontal",
          pageTextStyle: {
            color: "#ffffff", //翻页数字颜色
          },
          pageIconColor: "#2C86FF", //翻页箭头颜色
          pageIconInactiveColor: "rgba(44,132,251,0.40)", //翻页（即翻页到头时箭头的颜色）
          pageIconSize: 10,
          data: arry.data_3,
          selected: arry.data_4
        },
        grid: {
          left: "3%",
          right: "5%",
          bottom: "2%",
          top: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            data: arry.data_1,
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: arry.data_2,
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.echart {
  flex-grow: 1;
}
.Pechart {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>